.LandingPageMainSection {
    background-position: center;
    background-size: cover;
    width: 100%;
}
.LandingTitle {
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
    font-size: 70px;
    text-align: center;
    background-image: linear-gradient(45deg, #19bf23ff, #0467b7ff);
}
.ListBox {
    display: flex;
    justify-content: space-between;
    margin-left: 10%;
    margin-right: 20%;
}
.ListBox a {
    color:#f2f2f2;
    text-decoration: none;
}
.ListBox a:hover {
    text-decoration: underline;
}
.ListBox h2 {
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
    background-image: linear-gradient(45deg, rgb(25, 191, 114), #0467b7ff);
}
.websitesTable ol li {
    font-size: clamp(10px, 4vw, 20px);
}