.box {
  width: 100%;
}
.wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  height: 100%;
  color: #f2f2f2;
  box-sizing: border-box;
}
.appContainer {
  display: flex;
  width: clamp(100px, 100%, 1900px);
  margin-left: auto;
  margin-right: auto;
}
@media (min-width: 1900px) {
  .wrapper {
    border-right: 1px solid #747474 ;
  }
}
button {
  border: 1px solid #747474;
  background: none;
  color: white;
  border-radius: 5px;
  padding: 0 10px 0 10px;
  cursor: pointer;
}