.objectEntry {
    border: 1px solid #747474;
    border-radius: 10px;
    width: 800px;
    padding: 20px;
    display: grid;
    grid-template-columns: 60% 40%;
    grid-template-rows: 50px 1fr;
    margin: 10px;
}
.objectEntryTitle {
    font-size: 25px;
    font-weight: 500;
    grid-column: 1;
    grid-area: 1;
    border-bottom: 1px solid #747474;
    margin-right: 50px;
}
.objectEntryDescription {
    grid-column: 1;
    grid-row: 2;
}
.objectEntryImg {
    grid-column: 2;
    grid-row: 1/3;
    display: flex;
    justify-content: center;
    align-items: center;
}
.objectEntryImg img {
    width: 90%;
}
.infoResult {
    height: 500px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
}
.infoResult img {
    width: clamp(50px, 90%, 600px);
}
.entriesSearch {
    position: absolute;
    top: 50%;
    left: 20%;
    background: none;
    outline: none;
    border: none;
    border-bottom: 1px solid rgb(116, 116, 116);
    color: #f2f2f2;
    user-select: auto;
}
.entriesTitle {
    font-size: 40px;
    text-align: center;
    position: relative;
    margin-top: 10px;
    margin-bottom: 10px;
}
.entriesTitleBox {
    position: sticky;
    background-color: #25252d;
    top: 0;
    z-index: 10;
    border-bottom: 1px solid rgb(116, 116, 116);
}
@media (max-width: 800px) {
    .entriesTitleBox {
        display: flex;
        flex-direction: row-reverse;
        justify-content: center;
        align-items: center;
    }
    .entriesSearch {
        position: static;
        top: none;
        left: none;
        margin-right: 30px;


    }
    .objectEntryTitle {
        font-size: clamp(15px, 5.5vw, 25px);
        grid-area: 1/1/2/3;
        display: flex;
        padding: 20px;
        align-items: center;
        margin-right: 0;

    }
    .objectEntryDescription {
        font-size: clamp(10px, 3.5vw, 15px);
        grid-area: 2/1/3/3;
    }
    .objectEntry {
        grid-template-columns: 1fr;
        grid-template-rows: 50px auto 200px;
        padding: 10px;
    }
    .objectEntryImg {
        grid-area: 3/1/4/3;
        overflow: hidden;
        
    }
    .objectEntryImg img {
        width: clamp(50px, 80%, 200px);
    }
    .entriesTitle {
        font-size: clamp(15px, 6vw, 30px);
    }
}
@media (max-width: 500px) {
    .objectEntry {
        
        padding: 5px;
    }
}