.topic-items {
    color: white;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
}
.topic-item {
    /* margin-bottom: 10px; */
    width: 100%;
    border-top: 1px solid #747474;
    /* border-bottom: 1px solid #747474; */
}
.topic-item:first-child {
    border: none;
}
.topic-code {
    font-size: clamp(8px, .8vw, 15px);
    position: relative;
    background-color: #282c34;
}
.topic-code button {
    position: absolute;
    top: 5px;
    right: 5px;
    background: none;
    border: none;
    color: #747474;
    font-style: 20px;
    cursor: pointer;
}
.topic-code pre {
    margin: 0 !important;
}
.infoResult p {
    font-size: clamp(15px, 1.5vw, 30px);
}
.topic-title h3 {
    font-size: clamp(10px, 1.5vw, 25px);
}
.topic-title-data {
    text-align: left;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.topic-title {
    cursor: pointer;
    padding: 20px;
    background-color: #25252d;
    display: grid;
    grid-template-columns: 1fr 22%;
}
.topic-desc {
    display: grid;
    grid-template-columns: repeat(2, 50%);
    border-top: 1px solid #747474;
}
.topic-preview {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #282c34;
    flex-direction: row;
    border-left: 1px solid #747474;
}
.fillter-topic-items {
    color: white;
    position: sticky;
    background-color: #25252d;
    display: flex;
    border-bottom: 1px solid #747474;
    top: 0;
    z-index: 10;
    flex-direction: row;
    /* flex-wrap: wrap; */
}
.fillter-topic-items h1, .topic-title h3 {
    margin: 0;
    padding: 0;
}
.topic-title-text i {
    cursor: pointer;
}
.popupBox {
    border: 1px solid #747474;
    padding: 10px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    position: fixed;
    left: 50%;
    top: 50%;
    translate: -50% -50%;
    background-color: #25252d;
}
.popupBg {
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.2);
    position: fixed;
    left: 50%;
    top: 50%;
    translate: -50% -50%;
    z-index: 100;
    cursor: pointer;
}
textarea {
    resize: none;
}
.topic-loading {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 150px;
}
.topic-search {
    background: none;
    border: none;
    border-bottom: 1px solid #747474;
    outline: none;
    color: white;
}
.topic-bar-title {
    margin: 5px;
    font-style: clamp(10px, 1vw, 25px);
    width: 100%;
}
.fillter-item-box {
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: auto;
    font-size: clamp(10px, 2vw, 25px);
}
@media (max-width: 1300px) {
    .topic-desc {
        grid-template-columns: 1fr;
        grid-template-rows: repeat(2, auto);
    }
    .topic-preview {
        border-top: 1px solid #747474;
        border-left: 0;
        padding: 20px 0 20px 0;
    }
}
@media (max-width: 500px) {
    .topic-title {
        grid-template-columns: none;
        grid-template-rows: 1fr auto;
        padding: 10px;
    }
}
.topic-items-button {
    position: absolute;
    right: 5px;
    height: 100%;
}
.box-search {
    position: absolute;
    height: 100%;
    left: 10px;
}