.wrapperNav {
    min-width: 300px;
    display: flex;
    height: 100%;
    /* overflow: hidden; */
    flex-direction: column;
    border-right: 1px solid rgb(116, 116, 116);
    position: fixed;
    overflow-y: auto;
    -ms-overflow-style: scrollbar;
}
::-webkit-scrollbar {
    background-color: transparent;
    width: 10px;
}
::-webkit-scrollbar-thumb {
    background-color: #cccccc;
    border-radius: 50px;
}
::-webkit-scrollbar-thumb:hover {
    background-color: #aaaaaa;
}
.header {
    min-width: 300px;
    display: flex;
    justify-content: flex-end;
    color: #f2f2f2;
    /* color: #20252E; */
    /* position: fixed; */
    top: 0;
    left: 0;
    z-index: 100;
    user-select: none;
}
.navTop {
    height: 150px;
    display: flex;
    box-sizing: border-box;
    justify-content: center;
    align-items: center;

}
.clock {
    font-size: 30px;
    margin-left: 20px
}
.navBottom {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
}
.nav ul {
    list-style: none;
    margin: 0;
    padding: 0;
}
.nav ul li {
    padding: 5px 10px 5px 10px;
    display: flex;
}
.nav ul li a {
    color: white;
    text-decoration: none;
    font-size: 35px;
    padding: 5px 20px 5px 20px;
    border-radius: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.nav ul li span {
    padding-left: 5px;
}
.nav ul li a:hover {
    background-color: rgba(255, 255, 255, 0.1);
}
.navFriendList {
    margin-top: 20px;
    border-top: 1px solid #747474;
    width: 70%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;  
}
.navFriendList h2 {
    font-size: 35px;
    margin-top: 10px;
    margin-bottom: 0;
}
.navFriendList ul {
    list-style: none;
    margin-left: 0;
    padding-left: 0;
}
.navFriendList ul a {
    color: #f2f2f2;
    font-size: 20px;
    text-decoration: none;
}
.navFriendList ul a:hover {
    text-decoration: underline;
}
.entriesList {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}
.hamburger {
    position: fixed;
    display: none;
    flex-direction: column;
    top: 10px;
    left: 10px;
    gap: 5px;
}
.bar {
    width: 60px;
    height: 10px;
    background-color: #f2f2f244;
    border: 1px solid #747474;
    border-radius: 10px;
}
@media (max-width: 1000px) {
    .header, .wrapperNav {
        min-width: 100px;
    }
    .nav ul li a span {
        display: none;
    }
    .nav ul li {
        padding: 5px 0 5px 0;
    }
    .nav ul li a {
        padding: 5px 15px 5px 15px;
    }
    .clock {
        display: none;
    }
    .nav {
        margin-left: auto;
        margin-right: auto;
        padding: 0;
    }
    .navFriendList {
        display: none;
    }
}
@media (max-width: 600px) { 
    .header {
        position: static;
        width: 100%;
        height: 80px;
        background-color: #25252d;
    }
    .wrapperNav {
        position: static;
        width: 100%;
        height: 80px;
        border: none;
        border-bottom: 1px solid #747474;
        display: flex;
        justify-content: center;
        overflow-y: visible;
        overflow-x: auto;
    }
    .wrapperNav::-webkit-scrollbar {
        height: 5px;
    }
    .navTop {
        display: none;
    }
    .nav ul {
        display: inline-flex;
        /* margin-left: 0; */
        /* margin-right: 0; */
    }
    .nav ul li a {
        padding: 6px;
    }
    .nav ul li i {
        font-size: clamp(15px, 10vw, 50px);
    }
    .appContainer {
        flex-direction: column;
    }
}