.errorBox {
    height: calc(100vh - 100px);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 30px;
    color: #f2f2f2;
    flex-direction: column;
}
.errorBox h1 {
    font-size: 100px;
    margin-top: 0;
    margin-bottom: 0;
}
.btnError {
    font-size: 40px;
    background: none;
    border-radius: 50px;
    padding: 10px;;
    color: white;
    padding: 10px 15px 10px 15px;
    border: 1px white solid;
}
.btnError:hover {
    background-color: white;
    color: #25252d;
}